@import "../../styles/global";

.Cooperate {
  margin-top: 50px;
  padding: 50px 0;
  border-radius: 20px;
  background-color: $Dark-blue;
  color: $White-color;

  &-Title {
    h2 {
      padding: 50px 0;

      &::after {
        margin-top: 50px;
      }
    }

    p {
      position: relative;
      bottom: 130px;
      text-align: center;
      font-size: 1.4rem;
    }
  }

  &-Content {
    &-Block {
      display: grid;
      grid-template-columns: 1fr 1fr;
      max-width: 1140px;
      margin: 0 auto;
      gap: 30px;
      padding-top: 50px;

      li {
        font-weight: $Subtitle-font-weight;
        font-size: $Subtitle-font-size;
        line-height: $Subtitle-line-height;
        list-style-type: disc;
        list-style-position: inside;
        margin-bottom: 25px;

      }

      img {
        width: 100%;
        box-shadow: 5px 15px 40px 0 rgba(11, 24, 97, 0.32);
        margin-bottom: 25px;
      }
    }

    &-Button {
      margin-top: 25px;
    }
  }
}


@include desktop {
  .Cooperate {
    padding: 30px;

    &-Block {
      gap: 0;
    }

    &-Title {
      h2 {
        padding: 0;

        &::after {
          margin-bottom: 0;
        }
      }

      p {
        position: relative;
        bottom: 70px;
        text-align: center;
        font-size: 1rem;
      }
    }

    &-Content {
      &-Block {
        padding-top: 0;
        grid-template-columns: 1fr;
      }
    }
  }
}

@include mobile {
  img {
    display: none;
  }
}