@import "../../styles/global";


.ModalDefault {
  border-radius: 10px;

  background-color: $Dark-blue;
}


.Modal {


  &Header {


    h3 {
      text-align: center;
      text-transform: uppercase;
    }

    &__left {
    }

    &__center {
    }

    &__right {
    }
  }

  &Content {
    margin-top: 25px;
    display: flex;
    flex-direction: column;
    gap: 20px;


    input {
      font-weight: $Subtitle-font-weight;
      font-size: 1.1rem;
      line-height: $Subtitle-line-height;
      margin-top: 10px;
      border-radius: 10px;
      padding: 10px;


    }
  }

  &Footer {
    margin-top: 25px;
  }

  &Default {
    padding: 40px;
    color: $White-color;

    &__large {
      min-width: 768px;
      max-width: 768px;
      width: 100px;
    }


    &__medium {
      padding: 30px;
      min-width: 468px;
      max-width: 468px;
      width: 70px;
    }

    &__small {
      padding: 20px;
      min-width: 280px;
      max-width: 280px;
      width: 50px;
    }

    &-CloseButton {
      width: 20px;
      height: 20px;

      svg {
        fill: $Light-dark-blue;
        transition: all 0.15s ease-in-out;

        &:hover {
          fill: $Danger-color;
        }
      }

    }

  }


}