.slick-slider {
  max-width: 100%;
  max-height: 700px;
  margin: 0 auto;
  outline: transparent;
}

.slick-slider {
  position: relative;
  margin-bottom: 30px;
}

.slick-slider .slick-dots {
  display: flex !important;
  justify-content: center;
  align-items: center;
  bottom: 45px;
  @media (max-width: 1200px) {
    bottom: 0;
  }

}

.slick-slider .slick-li {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 5px;
  width: 8px;
  cursor: pointer;
}

.slick-slider .slick-dots li button {
  padding: 0;
  font-size: 0;
  max-width: 100%;
  border-radius: 50%;
  width: 8px;
  height: 8px;
  border: 1px solid white;
  background: transparent;
  outline: transparent;
  transition: all 0.15s;
  @media (max-width: 769px) {
    margin-top: 25px;
  }
  @media (max-width: 426px) {
    margin-top: 0;
    margin-bottom: 25px;
  }
}


.slick-slider .slick-dots li button:hover {
  border: 1px solid #808080;
}

.slick-slider .slick-dots li.slick-active button {
  background: white;
  outline: transparent;
}

.slick-slider .slick-dots li button:before,
.slick-slider .slick-dots li.slick-active button:before {
  color: transparent;
}

.slick-list,
.slick-track {
  touch-action: pan-y;

}

.slick-slider .slick-track,
.slick-slider .slick-list {
  touch-action: pan-y;

}

.slick-prev:before,
.slick-next:before {
  opacity: 0 !important;
}

.Slider {
  &-Arrow {
    position: absolute !important;
    top: 50% !important;
    z-index: 1 !important;

    &.slick-disabled {
      .Slider-ArrowButton {
        display: none;
      }
    }
  }

  &-ArrowButton {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 40%;
    transform: translateY(50%);
    z-index: 1;
    height: 40px;
    width: 40px;
    opacity: var(--slider-arrow-opacity);
    cursor: pointer;

    &__left {
      left: 7%;
    }

    &__right {
      right: 7%;
    }


  }

  &_ArrowCustom {
    svg {
      fill: white;
      width: 22px;
      height: 41px;
    }
  }
}
