@import "../../styles/global";


.Burger {
  display: flex;
  justify-content: center;
  align-content: center;
  position: relative;
  width: 27px;
  cursor: pointer;

  .Burger {
    position: relative;
    width: 27px;
    height: 3px;
    border-radius: 10px;
    transition: all 0.15s ease-in-out;

    &__orange {
      background-color: $Main-orange-color;

      &::before, &::after {
        background-color: $Main-orange-color;

      }
    }

    &__white {
      background-color: $White-color;

      &::before, &::after {
        background-color: $White-color;

      }
    }

    &::before, &::after {
      content: "";
      position: absolute;
      left: 0;
      width: 27px;
      height: 3px;
      border-radius: 10px;
      transition: all 0.15s ease-in-out;

    }

    &::before {
      transform: translateY(-10px);
    }

    &::after {
      transform: translateY(10px);
    }

    &__active {
      transform: translateX(-18px);
      background-color: transparent;
      box-shadow: none;


      &:before {
        transform: rotate(45deg) translate(10px, -10px);
      }

      &:after {
        transform: rotate(-45deg) translate(10px, 10px);
      }
    }
  }
}