@import "../../styles/global";


.Accordion {
  max-width: 100%;
  $this: &;
  margin-bottom: 10px;
  border-radius: 10px;
  border: 1px solid $Light-dark-blue;

  &-Header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1rem;
    padding: 10px 15px;
    color: $White-color;


    transition: all 0.3s ease;
    cursor: pointer;

    &:last-child {
      margin-bottom: 0;
    }

    &Title {
      font-weight: $Subtitle-font-weight;
      font-size: $Subtitle-font-size;
      line-height: $Subtitle-line-height;
    }

    &Icon {
      svg {
        width: 26px;
        height: 26px;
        transition: all 0.3s ease;
        fill: $White-color;
      }
    }
  }

  &-Content {
    padding: 15px 10px;
    color: white;
    border-radius: 0 0 10px 10px;
  }

  &#{$this}__active {
    transition: all 0.3s ease;
    background-color: white;

    #{$this}-Header {
      color: $Dark-blue;
      border-bottom: transparent;
      border-radius: 10px 10px 0 0;


      &Title {
        font-weight: 600;
      }
    }

    #{$this}-Content {
      color: $Dark-blue;

    }

    #{$this}-HeaderIcon {
      svg {
        transform: rotate(-90deg);
        fill: $Main-orange-color;
      }
    }
  }
}

