@import "../../styles/global";

.FormFields {

  &-Text {
    margin-bottom: 20px;

    h2 {
      margin-bottom: 20px;
      text-transform: uppercase;
    }

  }

  &-Field {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: auto;
    padding: 10px;
    position: relative;

    &-Input {
      padding: 20px;
      border-radius: 10px;
      border: 2px solid transparent;
      outline-color: $Main-orange-color;
      margin-top: 10px;
    }

    label {
      margin: 5px 0;
    }

    textarea {
      padding: 20px;
      height: 150px;
      border-radius: 10px;
      resize: none;
      outline-color: $Main-orange-color;
      margin: 20px 0;
    }
  }


  &-PhoneEmail {
    display: flex;
    align-items: flex-start;
    gap: 50px;
    flex-wrap: nowrap;
  }

  &-Buttons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
  }
}


@include tablet {
  .FormFields {
    text-align: center;

    &-PhoneEmail {
      gap: 0;
    }
  }
}

@include mobile {

  .FormFields {
    text-align: center;

    &-Text {
      p {
        font-size: 0.8rem;
      }
    }

    &-Field {
      label {
        display: none;
      }
    }

    &-PhoneEmail {
      display: block;
    }

    &-Buttons {
      flex-direction: column;
      gap: 25px;
    }
  }
}
