@import "../../styles/global";

.Footer {

  padding-top: 75px;
  padding-bottom: 25px;

  &-Content {
    color: $White-color;
    border-top: 2px solid $Main-orange-color;
    padding-top: 50px;
    display: flex;
    justify-content: space-between;
    max-width: 1140px;
    margin: 0 auto;

    &-LogoBlock {
      display: flex;
      flex-direction: column;
      gap: 5px;
      font-weight: $Subtitle-font-weight;
      font-size: $Subtitle-font-size;
      line-height: $Subtitle-line-height;
    }

    &-Item {
      li {
        margin: 15px 0;
        text-decoration: underline;
        color: $Light-dark-blue;
      }

      &__logos {
        margin-top: 10px;
        display: flex;
        gap: 10px;
      }
    }
  }
}

@include desktopSmall {
  .Footer {


    &-Content {
      justify-content: center;
      text-align: center;


      &-LogoBlock {
        align-items: center;
      }
    }
  }
}