@import "../../styles/global";

.Faq {
  max-width: 1140px;
  margin: 0 auto;

  &-Content {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;

    &-Accordion {
      width: 100%;
    }


    &-Form {
      padding: 30px;
      border-radius: 20px;
      background-color: $Dark-blue;
      box-shadow: 5px 15px 40px 0 rgba(11, 24, 97, 0.32);


      h3 {
        color: $White-color;
        margin-bottom: 10px;
      }

      h4 {
        color: $White-color;
        margin-bottom: 10px;
      }

      &-Field {
        position: relative;

        textarea {
          min-width: 100%;
          resize: none;
        }

        label {
          display: block;
          margin-bottom: 5px;
          color: $White-color;
        }

        p {
          display: none;
        }
      }

      &-Button {
        margin: 0 auto;
      }
    }
  }

}


@include tablet {
  .Faq {
    &-Content {
      flex-direction: column;

      &-Form {
        width: 100%;

        &-Inputs {
          display: grid;
          grid-template-columns: 1fr 1fr;
          align-content: center;
          gap: 15px;
          margin-bottom: 25px;
        }
      }
    }
  }
}

@include mobile {
  .Faq {
    &-Content {
      &-Accordion {
        width: 100%
      }
    }
  }
}

@include mobile {

  .Button {
    padding: 10px;
  }
}
