@import "../../styles/global";

.AboutUs {
  padding: 15px;
  display: flex;
  align-items: center;
  max-width: 1140px;
  margin: 0 auto;

  &-Image {
    width: 100%;

    img {
      margin-top: 50px;
    }
  }

  &-TextBlock {
    color: $White-color;

    p {
      font-size: 1.2rem;
    }
  }
}

@media screen and (max-width: 1025px) {
  .AboutUs {
    flex-direction: column;
    align-items: center;
    justify-content: center;

    &-Image {
      display: flex;
      align-items: center;
      justify-content: center;

      img {
        width: 75%;

      }
    }
  }
}

@media screen and (max-width: 769px) {
  img {
    display: none;

  }
}