@import "../../../styles/global";

.SliderMain {
  position: relative;

  &-Item {
    position: relative;

    img {
      max-width: 100%;
      margin: 0 auto;
    }

    &-Block {
      text-align: left;
      position: absolute;
      width: 480px;
      top: 20%;
      left: 23%;
      z-index: 2;
      color: white;

      h1 {
        font-weight: $Title-font-weight;
        font-size: $Main-Title-font-size;
        line-height: $Title-line-height;
        margin: 10px;
        text-transform: uppercase;
      }

      p {
        width: 345px;
        margin: 10px;
      }
    }
  }


  & > div {
    width: 100%;
  }

  &-Image {
    cursor: pointer;
    object-fit: cover;
    border-radius: 15px;
  }
}


@include desktop {
  .slick-slider {
    margin: 0 auto;
    outline: transparent;
  }

  .SliderMain {
    &-Item {
      
      img {
        height: 649px;
        object-fit: cover;
      }
    }
  }
}

@include tablet {
  .SliderMain-Item {

    &-Block {
      width: 300px;
      text-wrap: pretty;
      left: 10%;

      h1 {
        padding: 5px;
        font-size: 2rem;
      }

      p {
        padding: 5px;
      }
    }
  }
}


@include mobileSmall {
  .SliderMain-Item {

    &-Block {
      h1 {
        padding: 0;
        font-size: 2rem;
      }

      p {
        width: 250px;
        padding: 0;
      }
    }
  }
}