@import "../../styles/global";

.Button {
  text-wrap: nowrap;
  padding: 15px 40px;
  cursor: pointer;
  border-radius: 90px;
  font-weight: 600;
  font-size: 15px;
  text-align: center;
  color: #fff;
  background: $Main-orange-color;
  transition: all 0.2s ease-out;
  border: none;


  &-with-icon {
    display: flex;
    align-items: center;
    gap: 10px;

    svg {
      fill: $White-color;
    }

  }

  &-transparent {
    background: transparent;
    border: 1px solid $Main-orange-color;

    &:hover {
      background: $Hover-color;
    }
  }

  &:hover {
    background: $Hover-color;
  }
}

@include mobileSmall {
  .Button {
    padding: 10px 20px;

    &:hover {
      background: $Main-orange-color;
    }

    &-transparent {
      &:hover {
        background: none;
      }
    }

  }
}