@import "../../../styles/global";

.Services {
  padding: 50px 0;
  border-radius: 20px;
  background-color: $Dark-blue;


}

.SliderServices {
  max-width: 1140px;
  padding: 50px 0;
  margin: 0 auto;

  display: flex;
  justify-content: center;
  align-items: center;


  &-Item {
    padding: 0 10px;
    position: relative;


    img {
      width: 100%;

    }

    &-Block {
      display: flex;
      flex-direction: column;
      justify-content: center;
      position: absolute;
      bottom: 10%;
      left: 10%;
      color: $White-color;

      h3 {
        margin-bottom: 20px;
      }


      p {
        width: 300px;
        margin-bottom: 20px;
      }
    }


  }

  .Slider {

    &-ArrowButton {


      &__left {
        left: -5%;
      }

      &__right {
        right: -5%;
      }
    }
  }
}

@include desktop {
  .SliderServices {
    padding: 50px;

    &-Item {
      &-Block {

      }
    }
  }
}

@include tablet {
  .SliderServices {
    padding: 25px;

    &-Item {
      &-Block {
        h3 {
          font-size: 1.1rem;
        }

        p {
          width: 240px;
          font-size: 0.8rem;
          text-wrap: auto;

        }
      }
    }
  }
}

@include mobile {
  .Services {
    padding-top: 0;
  }

  .SliderServices {
    padding-bottom: 35px;

    &-Item {
      position: relative;

      h3 {
        font-size: 1rem;
        width: 200px;
      }

      p {
        width: 200px;
        font-size: 0.8rem;
      }
    }
  }
}

@include mobileSmall {
  .SliderServices {
    padding: 0;

    &-Item {
      &-Block {

        h3 {
          font-size: 1rem;
          margin-bottom: 150px;
        }

        p {
          display: none;
        }
      }
    }
  }
}