@import "../../styles/global";

.WhyWe {
  padding: 80px 50px;
  border-radius: 20px;
  background-color: $Dark-blue;

  &-Items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 80px;
    justify-items: center;
    align-items: flex-start;
    max-width: 1140px;
    margin: 0 auto;

    &-Item {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin-top: 25px;

      p {
        color: $White-color;
        padding: 10px 20px;
        margin-top: 30px;
        text-align: center;
      }

      &__icon {
        position: relative;
        background-color: #89c249;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        width: 100px;
        height: 100px;

        &::before {
          border-radius: 50%;
          content: '';
          position: absolute;
          left: -10px;
          top: -10px;

          width: 120px;
          height: 120px;

          border: 2px solid $Light-dark-blue;
        }

        svg {
          width: 50px;
          height: 50px;

        }


      }

    }
  }
}

@include desktopSmall {
  .WhyWe {
    &-Items {
      grid-template-columns: 0.5fr 0.5fr;
    }
  }
}

@include mobile {
  .WhyWe {
    padding: 25px;
    text-align: center;

    &-Items {
      justify-content: center;
      grid-template-columns: 0.5fr;

      &-Item {
        padding-bottom: 25px;
      }

    }
  }

}

