@import "../../styles/global";

body.lock-scroll {
  overflow: hidden !important;
  position: fixed;
  width: 100%;
}

.Header {
  padding: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;

  &-Burger {
    visibility: hidden;
  }

  &__MenuList {
    cursor: pointer;
    display: flex;
    gap: 3rem;
    list-style-type: none;
    color: white;
    font-size: 16px;
    margin: 0 auto;
    flex-wrap: nowrap;

    ul {
      display: flex;
      align-items: center;
      gap: 3rem;
    }

    li {
      flex-wrap: nowrap;
      transition: all 0.2s ease-out;

      &:hover {
        color: #e58d35;
      }
    }
  }
}

@include desktop {

  .Header {
    padding: 50px 50px;

    &__MenuList {
      ul {
        gap: 1.2rem;
        text-wrap: nowrap;
      }
    }

    &__Button {
      padding: 15px;
    }
  }

}


@include desktopSmall {
  .Header {
    padding: 30px 20px;

    &-Burger {
      z-index: 15;
      visibility: visible;
    }

    &__MenuList {
      z-index: 10;
      position: fixed;
      top: 10%;
      left: 0;
      height: 700px;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      background-color: $Dark-blue;
      transition: 0.5s;
      transform: translateY(-200%);
      box-shadow: 14px 22px 40px 0 rgba(30, 35, 66, 0.28);
      padding-bottom: 35px;
      overflow: scroll;

      &:before {
        z-index: -1;
        position: fixed;
        top: 90%;
        left: 0;
        content: "";
        background-color: $More-dark-blue;
        width: 100%;
        height: 50%;
      }


      ul {
        padding: 40px 20px;
        flex-direction: column;
      }

      li {
        font-size: 1.5rem;
        margin-bottom: 10px;
      }

      &__open {
        transform: none;

      }
    }


  }

}

@include tablet {
  .Header {

    &__MenuList {
      padding-bottom: 35px;

      li {
        flex-wrap: nowrap;
        transition: all 0.2s ease-out;

        &:hover {
          color: white;
        }
      }

      button {
        padding: 15px 40px;
        font-size: 1rem;
      }
    }
  }
}

@include mobile {
  .Header {
    padding: 20px;

    &__MenuList {
    }
  }
}