@import '../../styles/global';

.CostCalculation {
  display: flex;
  justify-content: center;
  padding-top: 100px;

  h2 {
    position: relative;

    &::after {
      position: absolute;
      margin-top: 50px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  &-Modal {
    &Content {
      margin-top: 25px;
      display: flex;
      flex-direction: column;
    }
  }


  &-Steps {
    display: flex;
    gap: 30px;
    position: relative;
    top: 20px;

    &__active {
      background-color: #89c249 !important;
      border: 2px solid transparent;
    }

    span {
      background-color: $More-dark-blue;
      z-index: 5;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 60px;
      height: 60px;
      border-radius: 50%;
      color: $White-color;
      font-weight: 700;
      font-size: 24px;
      line-height: 126%;
      border: 2px solid $Dark-blue;
    }
  }

  &-Man {
    max-width: 100%;
  }

  &-Form {
    width: 100%;
    z-index: 1;
    height: 650px;
    color: white;
    padding: 30px;
    border-radius: 20px;
    background-color: $Dark-blue;
    display: grid;
    align-items: center;


    &__top {
      display: grid;
      grid-template-columns: 1fr 1fr;
    }

    &__icons {
      padding: 15px;
      display: flex;
      gap: 10px;

      svg {
        cursor: pointer;
        width: 30px;
        height: 30px;
        transition: all 0.3s;

        &:hover {
          fill: $Main-orange-color;
        }
      }
    }

    &__button {
      margin-bottom: 30px;
    }

    &__checkbox {
      padding-top: 25px;
      display: flex;
      justify-content: space-between;

      &-item {
        padding: 10px;
        display: flex;
        align-items: center;
        justify-content: flex-start;

        label {
          color: $White-color;
        }

        input {
          appearance: none;
          background: #fff;
          border: 1px solid $More-dark-blue;
          min-width: 24px;
          min-height: 24px;
          border-radius: 4px;
          vertical-align: text-bottom;
          position: relative;

          &::before {
            content: "";
            position: absolute;
            margin: auto;
            left: 0;
            right: 0;
            bottom: 0;
            overflow: hidden;
            top: 0;
          }

          &:checked::before {
            border-right: 3px solid $More-dark-blue;;
            border-bottom: 3px solid $More-dark-blue;;
            height: 70%;
            width: 30%;
            transform: rotate(40deg) translateY(-15%) translateX(-10%);
          }
        }
      }
    }

    &__item {
      position: relative;
      padding: 10px;
      display: flex;
      flex-direction: column;

      &-Input {
        padding: 20px;
        font-size: 15px;
        border-radius: 10px;
        border: 2px solid transparent;
        outline-color: $Main-orange-color;
        margin-top: 10px;
      }

      label {
        font-size: 15px;
        display: flex;
        text-wrap: nowrap;
        justify-content: space-between;
        color: $White-color;
        margin: 5px 0;

        span {
          svg {
            cursor: pointer;
            fill: $Main-orange-color;
            width: 14px;
            height: 14px;

          }
        }
      }
    }
  }
}


@include desktopLarge {
  .CostCalculation {
    h2 {
      &::after {
        left: 80%;
      }
    }
  }
}

@include desktop {
  .CostCalculation {
    display: block;

    h2 {
      &::after {
        top: 75%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }

    &-Block {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-bottom: 50px;
    }

    &-Man {
      display: none;
    }
  }
}


@include tablet {
  .CostCalculation {

    h2 {
      padding-bottom: 25px;
    }

    &-Form {
      &__checkbox {
        flex-direction: column;
        width: 100%;
      }
    }
  }
}


@include mobile {
  .CostCalculation {
    padding-top: 25px;

    &-Form {
      padding: 15px;
      height: 100%;

      &__item {
        padding: 5px;
      }

      &__top {
        display: block;
      }

      &__middle {
        display: block;
        margin: 25px 0;
      }
    }

    h2 {
      &::after {
        top: 75%;
        left: 50%;
        transform: translate(-50%, -75%);
      }
    }
  }
}