@import "../../../styles/global";

.ThanksForOrder {
  &-Content {
    color: white;

    h2 {
      font-size: 3rem;
      margin: 20px 0;
    }

    p {
      font-size: 2rem;
    }

  }

  &-Icon {
    margin: 40px;

    svg {
      width: 40%;
      height: 40%;
    }
  }
}

@include tablet {
  .ThanksForOrder {
    &-Content {

      h2 {
        font-size: 2.5rem;
      }

      p {
        font-size: 1.5rem;
      }
    }
  }
}

@include mobile {
  .ThanksForOrder {
    &-Content {
      text-align: center;

      h2 {
        font-size: 2rem;
      }

      p {
        font-size: 1rem;
      }
    }

    &-Icon {

      svg {
        width: 100%;
        height: 100%;
      }
    }
  }
}

