@import "../../../styles/global";

.Delivery {
  padding: 50px 0;
}


.SliderDelivery {
  padding: 50px 0;
  max-width: 1140px;
  margin: 0 auto;

  &-Item {
    padding: 0 10px;

    img {
      width: 100%;
    }
  }


  &-Image {
    width: 360px;
  }

  &-Block {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    text-align: center;
    color: $White-color;
    padding: 20px 0;
    background-color: $Dark-blue;
    border-radius: 0 0 20px 20px;
    font-size: 17px;

    span {
      font-weight: 400;
      font-size: 22px;
      line-height: 125%;
    }

    &__icon {
      display: flex;
      align-items: center;

      p {
        margin: 0 10px;
      }
    }
  }

  &-Buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 10px;

    &__small {
      margin: 0 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: 400;
      font-size: 15px;
      line-height: 146%;
      width: 135px;
    }
  }

  .Slider {

    &-ArrowButton {

      &__left {
        left: -5%;
      }

      &__right {
        right: -5%;
      }
    }
  }
}

@include tablet {

  .SliderDelivery {
    padding-top: 0;
    padding-bottom: 30px;

    h3 {
      font-size: 1rem;
    }

    &-Buttons {
      gap: 10px;

      button {
        font-size: 0.8rem;
        padding: 10px 20px;
        width: 110px;
      }

      &__small {
        margin: 0;
      }
    }
  }
  
}


@include mobile {
  .SliderDelivery {
    padding-top: 0;
    padding-bottom: 50px;
  }
}

@include mobileSmall {

  .SliderDelivery {
    padding-bottom: 35px;

    &-Block {
      p {
        font-size: 0.8rem;
      }

      &__icon {

      }
    }

    h3 {
      font-size: 15px;
    }

    &-Buttons {
      gap: 8px;

    }
  }

}