//Custom React-Toastify colors
:root {
  --toastify-color-success: #89c249;
  --toastify-color-dark: #363a4d;
}


//Colors//
$Main-orange-color: #e58d35;
$Hover-color: #e87302;
$White-color: #ffffff;
$Light-dark-blue: #6C7188;
$Dark-blue: #363a4d;
$More-dark-blue: #282B38;
$Danger-color: #E53535;
$Success-color: #89c249;


//TitleFont//
$Main-Title-font-size: 3rem;
$Title-font-weight: 700;
$Title-font-size: 2rem;
$Title-line-height: 1.25;

$MiniTitle-font-size: 1.2rem;

//SubtitleFont//
$Subtitle-font-weight: 400;
$Subtitle-font-size: 1rem;
$Subtitle-line-height: 1.36;

//LabelFont//
$Label-font-weight: 600;
$Label-font-size: 0.9rem;
$Label-line-height: 1.26;


//Global
html {
  box-sizing: border-box;
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  font-size: 16px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  height: 100vh;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: "Noto Sans", serif;
  font-size: 14px;
  font-weight: 400;
  font-style: normal;
  line-height: 1.25;
  background: $More-dark-blue;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
  font-weight: $Subtitle-font-weight;
  font-size: $Subtitle-font-size;
  line-height: $Subtitle-line-height;
}

a {
  display: inline-block;
  text-decoration: none;
  color: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

h3, h2 {
  font-weight: $Title-font-weight;
  font-size: $MiniTitle-font-size;
  line-height: $Title-line-height;
}

p {

  font-weight: $Subtitle-font-weight;
  font-size: $Subtitle-font-size;
  line-height: $Subtitle-line-height;
}

label {
  font-weight: $Label-font-weight;
  font-size: $Label-font-size;
  line-height: $Label-line-height;
}

section {
  margin: 30px 0;
}

button {
  cursor: pointer;
  outline: transparent;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 30px;
}


// Adaptive
$breakpoint-mobileSmall: 321px;
$breakpoint-mobile: 481px;
$breakpoint-tablet: 769px;
$breakpoint-desktopSmall: 992px;
$breakpoint-desktop: 1201px;
$breakpoint-desktopLarge: 1441px;
$breakpoint-desktopFH: 1921px;


@mixin mobileSmall {
  @media screen and (max-width: $breakpoint-mobileSmall) {
    @content;
  }
}

@mixin mobile {
  @media screen and (max-width: $breakpoint-mobile) {
    @content;
  }
}


@mixin tablet {
  @media screen and (max-width: $breakpoint-tablet) {
    @content;
  }
}

@mixin desktopSmall {
  @media screen and (max-width: $breakpoint-desktopSmall) {
    @content;
  }
}

@mixin desktop {
  @media screen and (max-width: $breakpoint-desktop) {
    @content;
  }
}

@mixin desktopLarge {
  @media screen and (max-width: $breakpoint-desktopLarge) {
    @content;
  }
}

@mixin desktopFH {
  @media screen and (max-width: $breakpoint-desktopFH) {
    @content;
  }
}