@import "../../styles/global";

.Title {


  &-Item {
    display: flex;
    flex-direction: column;
    align-items: center;
  }


  h2 {
    font-weight: $Title-font-weight;
    font-size: $Title-font-size;
    line-height: $Title-line-height;
    color: $White-color;
    text-transform: uppercase;
    margin-bottom: 40px;
    margin-top: 20px;


    &::after {
      margin-top: 25px;
      content: "";
      display: block;
      width: 85px;
      height: 2px;
    }


  }

  &_green {
    &::after {
      background-color: $Success-color;
    }
  }

  &_orange {
    &::after {
      background-color: $Main-orange-color;
    }
  }
}


@include desktop {
  .Title {
    text-align: center;

    h2 {
      font-size: 1.8rem;
    }
  }
}

@include tablet {
  .Title {
    h2 {
      font-size: 1.4rem;
    }
  }
}


@include mobile {
  .Title {
    padding-top: 20px;

    h2 {
      text-align: center;
      margin-bottom: 20px;
    }
  }

}

