@import "../../styles/global";

.Input {
  $this: &;
  display: flex;
  align-items: center;
  width: 100%;
  transition: all 0.25s ease;
  border: 2px solid transparent;
  padding: 20px;
  margin: 10px 0;
  font-size: 15px;
  border-radius: 10px;
  outline-color: $Main-orange-color;

  &__error {
    z-index: 5;
    border: 2px solid $Danger-color;
    outline-color: $Danger-color;
  }
}

.error {
  position: absolute;
  z-index: 5;
  bottom: -5px;
  left: 10px;
  margin-top: 10px;
  font-size: 15px;
  background: transparent;
  color: $Danger-color;
}


.active {
  color: $Main-orange-color;
}


@include mobileSmall {
  .Input {
    padding: 10px;
    margin: 5px 0;
  }
  .error {
    bottom: -10px;
  }
}